<template>
  <div class="container">
    <label class="label" :for="id">
      <span class="title">{{ label }}</span>
      <span
        v-if="max"
        :class="[isOverflow ? 'symbols-overflow' : null, 'symbols']"
        >{{ symbolsCounter }}</span
      >
    </label>
    <slot :id="id" />
  </div>
</template>

<script>
import { generateId } from '@/utils/generateId';

export default {
  setup() {
    return {
      id: generateId(),
    };
  },
  props: {
    label: {
      type: String,
      required: true,
    },
    value: String,
    max: String,
  },
  computed: {
    isOverflow() {
      return this.value.length > this.max;
    },
    symbolsCounter() {
      return `${this.value.length}/${this.max}`;
    },
  },
  components: {},
};
</script>

<style lang="scss" scoped>
.container {
  .label {
    margin-bottom: 6px;
    display: flex;
    justify-content: space-between;

    .title {
      font-weight: 600;
      font-size: 16px;
      line-height: 171%;
      color: #25262c;
    }

    .symbols {
      font-weight: 500;
      font-size: 12px;
      line-height: 171%;
      color: #989898;

      &-overflow {
        color: #ec675c;
      }
    }
  }
}
</style>
