<template>
  <div class="container">
    <div
      v-for="color in colorList"
      :key="color"
      @click="() => onChangeColor(color)"
      class="colorCheckbox"
      :style="{ backgroundColor: color }"
    >
      <CheckMarkIcon v-if="color === activeColor" />
    </div>
  </div>
</template>

<script>
import CheckMarkIcon from '@/components/icons/CheckMarkIcon.vue';

export default {
  props: {
    colorList: {
      required: true,
      type: [String],
    },
  },
  data() {
    return {
      activeColor: this.colorList[0],
    };
  },
  methods: {
    onChangeColor(color) {
      this.activeColor = color;
    },
  },
  components: { CheckMarkIcon },
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  .colorCheckbox {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 5px;
    }
  }
}
</style>
