<template>
  <div class="timerByBlocks">
    <div v-for="{ value, label } in countdownMapper" :key="label">
      <div class="valueCard">{{ value }}</div>
      <div class="label">{{ label }}</div>
    </div>
  </div>
</template>

<script>
import { useAppStore } from '@/stores/useAppStore';

export default {
  setup() {
    return {
      appStore: useAppStore(),
    };
  },
  computed: {
    countdownMapper() {
      const { days, hours, minutes, seconds } = this.appStore.countdown;

      return [
        { value: days, label: 'days' },
        { value: hours, label: 'hours' },
        { value: minutes, label: 'minutes' },
        { value: seconds, label: 'seconds' },
      ];
    },
  },
};
</script>

