<template>
  <div class="banner">
    <div class="main_column">
      <div class="banner_demo" ref="bannerDemo">
        <div class="banner_demo_wrapper" v-if="bannerStatus === 'success'">
          <BannerCanvas
            v-for="canvasItem in canvases"
            :key="canvasItem.title"
            :canvas="canvasItem"
            :scheme="banner?.schemes[0]?.id"
            :banner="banner"
            :zoom="bannerDemoWidth / canvasItem.width"
            ref="canvas"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import domtoimage from 'dom-to-image';
import BannerCanvas from '@/components/BannerCanvas.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useCategoriesStore } from '@/stores/useCategoriesStore';
import { useBannersStore } from '@/stores/useBannersStore';
import { useToaster } from '@/utils/useToaster';
import Client from '@/api/index';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      categoriesStore: useCategoriesStore(),
      bannersStore: useBannersStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    zoom: 1,
    uploadProgress: 0,
    isSubmitting: false,
    isSubmitSuccess: '',
    bannerDemoWidth: 0,
    bannerDemoResizeObserver: null,
  }),
  props: ['id'],
  methods: {
    async getBanner() {
      try {
        await this.bannersStore.getBanner(this.id);
      } catch (error) {
        console.error(error);
        this.toaster.error('Не получилось загрузить баннер :(');
      }
    },
    addColorScheme(color) {
      this.bannersStore.addColorScheme(color, this.$refs.schemes);
    },
    selectCanvas(id) {
      this.bannersStore.setCanvasId(id);
      this.$nextTick(() => {
        this.scrollToCanvasCenter();
      });
    },
    async submit() {
      if (this.isSubmitting) {
        return;
      }

      const {
        conversion,
        category,
        userGroup,
        canvases,
        schemes,
        layers,
        files,
      } = this.banner;

      try {
        this.isSubmitting = true;
        this.uploadProgress = 1;
        const config = {
          onUploadProgress: (progressEvent) => {
            const progress = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total,
            );
            this.uploadProgress = progress > 98 ? 98 : progress;
          },
        };
        // const scale = 2;
        const canvasEl = this.$refs.canvas[0].$el.querySelector('.canvas');
        const preview = await domtoimage.toPng(canvasEl, {
          // width: this.$refs.canvas.clientWidth * scale,
          // height: this.$refs.canvas.clientHeight * scale,
          // style: {
          //   transform: `scale(${scale})`,
          //   transformOrigin: 'top left',
          // },
        });
        const banner = {
          files,
          conversion,
          preview: preview.split(',')[1],
          category_id: category?.value,
          is_premium: userGroup?.value === 'premium',
          data: {
            canvases,
            schemes,
            layers,
          },
        };

        if (this.id) {
          banner.id = this.id;
          await Client.banners.update(banner, config);
          this.toaster.success('Изменения сохранены!');
        } else {
          await Client.banners.create(banner, config);
          this.$router.push({ name: 'adminBanners' });
          this.toaster.success('Баннер добавлен!');
        }
      } catch (error) {
        console.log(error);
        this.toaster.error('Не получилось сохранить :(');
      } finally {
        this.isSubmitting = false;
        this.uploadProgress = 0;
      }
    },
    async init() {
      this.appStore.toggleAppLoader(true);

      if (this.id) {
        await this.getBanner();
      } else {
        this.bannersStore.setUserGroup(0);
      }

      const preloadEl =
        this.$refs.canvas[0].$el.querySelector('.canvas_preload');
      await domtoimage.toPng(preloadEl);

      this.bannerDemoResizeObserver = new ResizeObserver((entries) => {
        for (let entry of entries) {
          this.bannerDemoWidth = entry.contentRect.width;
        }
      });
      this.bannerDemoResizeObserver.observe(this.$refs.bannerDemo);

      setTimeout(() => {
        this.appStore.toggleAppLoader(false);
      }, 135);
    },
    changeZoom(key) {
      switch (key) {
        case '=':
          this.zoom += 0.05;
          break;

        case '-':
          this.zoom -= 0.05;
          break;

        default:
          this.zoom = 1;
      }
    },

    calculateBannerDemoWidth() {
      this.bannerDemoWidth = this.$refs.bannerDemo.offsetWidth;
    },
  },
  computed: {
    banner() {
      return this.bannersStore.banner.data;
    },
    bannerStatus() {
      return this.bannersStore.banner.status;
    },
    currentCanvasId() {
      return this.bannersStore.currentCanvasId;
    },
    currentSchemeId() {
      return this.bannersStore.currentSchemeId;
    },
    canvases() {
      return this.bannersStore.canvases;
    },
    canvas() {
      return this.bannersStore.canvas;
    },
    schemes() {
      return this.bannersStore.schemes;
    },
    layers() {
      return this.banner.layers.map((layer) => layer).reverse();
    },
    activeLayer() {
      return this.bannersStore.activeLayer;
    },
    categories() {
      return this.categoriesStore.categories.data;
    },
    listCategories() {
      return this.categories.map(({ id, name }) => {
        return {
          label: name,
          value: id,
        };
      });
    },
  },
  mounted() {
    this.init();
    this.calculateBannerDemoWidth();
  },
  beforeUnmount() {
    this.bannerDemoResizeObserver.disconnect();
  },
  components: {
    BannerCanvas,
  },
  watch: {
    categories: {
      deep: true,
      immediate: true,
      handler() {
        this.banner.category = this.listCategories[0];
      },
    },
  },
};
</script>

